<template>
  <div id="app">
    <!-- 视频背景 -->
    <video class="background-video" autoplay muted loop>
      <source src="/background.mp4" type="video/mp4">
      <!-- 如果浏览器不支持视频，显示备用背景 -->
    </video>
    
    <!-- 导航栏 -->
    <nav class="navbar">
      <div class="nav-container">
        <div class="nav-logo">
          <h2>Kercy's Blog</h2>
        </div>
        <ul class="nav-menu" :class="{ active: isMenuOpen }">
          <li class="nav-item">
            <a href="#" class="nav-link" @click="setActiveTab('home')">项目</a>
          </li>
          <li class="nav-item">
            <a href="#" class="nav-link" @click="setActiveTab('categories')">分类</a>
          </li>
          <li class="nav-item">
            <a href="#" class="nav-link" @click="setActiveTab('about')">关于</a>
          </li>
        </ul>
        <div class="nav-toggle" @click="toggleMenu">
          <span class="bar"></span>
          <span class="bar"></span>
          <span class="bar"></span>
        </div>
      </div>
    </nav>

    <!-- 主要内容区域 -->
    <main class="main-content">
      <div class="container">
        <div class="blog-layout">
          <!-- 左侧主内容 -->
          <div class="content-area">
            <!-- 首页 - 博客文章列表 -->
            <div v-if="activeTab === 'home'" class="blog-posts">
              <h1 class="page-title">
                {{ selectedCategory ? `${selectedCategory} 分类文章` : '全部文章' }}
                <button v-if="selectedCategory" @click="clearCategoryFilter" class="clear-filter-btn">
                  <i class="fas fa-times"></i> 清除筛选
                </button>
              </h1>
              <div v-if="searchQuery && filteredPosts.length === 0" class="no-results">
                <p>没有找到相关文章，请尝试其他关键词。</p>
              </div>
              <article v-for="post in filteredPosts" :key="post.id" class="blog-post" @click="openPost(post)">
                <div class="post-header">
                  <h2 class="post-title">{{ post.title }}</h2>
                  <div class="post-meta">
                  <span class="post-date">
                    <i class="far fa-calendar"></i> {{ post.date }}
                  </span>
                  <span class="post-category">
                    <i class="fas fa-tag"></i> {{ post.category }}
                  </span>
                  <a v-if="post.github" :href="post.github" target="_blank" class="github-link" @click.stop>
                    <i class="fab fa-github"></i> 查看源码
                  </a>
                </div>
                </div>
                <div class="post-excerpt">
                  {{ post.excerpt }}
                </div>
              </article>
            </div>

            <!-- 分类页面 -->
            <div v-if="activeTab === 'categories'" class="categories">
              <h1 class="page-title">文章分类</h1>
              <div class="category-grid">
                <div v-for="category in categories" :key="category.name" class="category-card" @click="filterByCategory(category.name)">
                  <div class="category-icon">
                    <i :class="category.icon"></i>
                  </div>
                  <h3>{{ category.name }}</h3>
                  <p>{{ category.count }} 篇文章</p>
                </div>
              </div>
            </div>

            <!-- 关于页面 -->
            <div v-if="activeTab === 'about'" class="about">
              <h1 class="page-title">关于我</h1>
              <div class="about-content">
                <div class="about-card">
                  <div class="about-header">
                    <div class="avatar">
                      <i class="fas fa-user-circle"></i>
                    </div>
                    <h2>{{ personalInfo.name }}</h2>
                    <p class="subtitle">{{ personalInfo.university }}</p>
                  </div>
                  <div class="about-body">
                    <p>{{ personalInfo.description }}</p>
                    
                    <div class="skills-section">
                      <h3>技术栈</h3>
                      <div class="skills-grid">
                        <span v-for="skill in techStack" :key="skill" class="skill-tag">
                          {{ skill }}
                        </span>
                      </div>
                    </div>
                    
                    <div class="contact-section">
                      <h3>联系方式</h3>
                      <div class="contact-links">
                        <a :href="personalInfo.github" target="_blank" class="contact-link">
                          <i class="fab fa-github"></i>
                          GitHub
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- 右侧边栏 -->
          <aside class="sidebar">
            <!-- 搜索框 -->
            <div class="widget search-widget">
              <h3>搜索项目</h3>
              <div class="search-box">
                <input type="text" v-model="searchQuery" placeholder="输入关键词搜索..." @input="searchPosts">
                <button><i class="fas fa-search"></i></button>
              </div>
            </div>

            <!-- 标签云 -->
            <div class="widget tags-widget">
              <h3>标签云</h3>
              <div class="tag-sphere-container" @mousemove="onMouseMove">
                <!-- 球体主轮廓 -->
                <div class="sphere-outline"></div>
                
                <!-- 地轴 - 穿过南北极，倾斜23.5度 -->
                <div class="earth-axis"></div>
                
                <!-- 经纬线网格容器 -->
                <div class="grid-container" id="gridContainer">
                  <!-- 经线将在JavaScript中动态生成 -->
                </div>
                
                <!-- 标签球体 -->
                <div class="tag-sphere" id="tagSphere">
                  <span 
                    v-for="(tag, index) in tags" 
                    :key="tag.name" 
                    :class="['tag-3d', { 'tag-hovered': hoveredTag === index }]"
                    @mouseenter="onTagHover(index)" 
                    @mouseleave="onTagLeave"
                    @click="filterByTag(tag.name)"
                    :style="{ 
                      pointerEvents: 'auto',
                      position: 'absolute',
                      zIndex: hoveredTag === index ? 9999 : 'auto'
                    }"
                  >
                    {{ tag.name }}
                  </span>
                </div>
                
                <!-- 球体光晕效果 -->
                <div class="sphere-glow"></div>
                
                <!-- 水平椭圆 - 与球体相切 -->
                <div class="horizontal-ellipse"></div>
              </div>
            </div>
          </aside>
        </div>
      </div>
    </main>

    <!-- 文章详情模态框 -->
    <div v-if="selectedPost" class="modal-overlay" @click="closePost">
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h2>{{ selectedPost.title }}</h2>
          <button class="close-btn" @click="closePost">
            <i class="fas fa-times"></i>
          </button>
        </div>
        <div class="modal-body">
          <div class="post-meta">
            <span><i class="far fa-calendar"></i> {{ selectedPost.date }}</span>
            <span><i class="fas fa-tag"></i> {{ selectedPost.category }}</span>
          </div>
          <div class="post-content" v-html="selectedPost.content"></div>
        </div>
      </div>
    </div>

    <!-- 页脚 -->
    <footer class="footer">
      <div class="container">
        <div class="footer-content">
          <p>&copy; 2025 Kercy Owned.</p>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
import { ref, computed, onMounted, onUnmounted } from 'vue'

export default {
  name: 'App',
  setup() {
    const isMenuOpen = ref(false)
    const activeTab = ref('home')
    const selectedPost = ref(null)
    const searchQuery = ref('')
    const selectedCategory = ref(null) // 添加分类筛选变量
    
    // 3D标签球相关
    const hoveredTag = ref(-1)
    const rotationSpeed = ref(1)
    let animationId = null
    let earthTilt = 0 // 地轴竖直，无倾斜

    // 个人项目数据（基于GitHub仓库）
    const blogPosts = ref([
      {
        id: 1,
        title: 'CS106B_2022Winter - Stanford计算机科学课程',
        excerpt: 'Stanford CS106B课程完整资源包，包含讲座、作业和Qt环境配置。这是我学习数据结构与算法的重要里程碑。',
        content: '<p>这个项目包含了Stanford CS106B (Winter 2022)的完整学习资源...</p><p>通过这门课程，我深入学习了C++编程和数据结构算法。</p>',
        date: '2024-02-10',
        category: '学习项目',
        isLiked: false,
        tags: ['C++', 'Algorithm', 'Stanford', 'Qt'],
        github: 'https://github.com/KercyDing/CS106B_2022Winter'
      },
      {
        id: 2,
        title: 'Questria - 跨平台AI聊天应用',
        excerpt: '基于Flutter开发的跨平台AI聊天应用，支持与多种AI大模型进行对话。这是我在移动开发领域的重要实践。',
        content: '<p>Questria是我使用Flutter开发的AI聊天应用...</p><p>支持多种AI模型，提供流畅的跨平台体验。</p>',
        date: '2024-05-16',
        category: '移动开发',
        isLiked: false,
        tags: ['Flutter', 'Dart', 'AI', 'Mobile'],
        github: 'https://github.com/KercyDing/Questria'
      },
      {
        id: 3,
        title: 'Nefelibata - AI API图形化界面',
        excerpt: 'GLM和DeepSeek API的图形化界面应用，让AI交互更加直观便捷。使用Python开发的桌面应用程序。',
        content: '<p>Nefelibata为AI API提供了友好的图形界面...</p><p>支持GLM和DeepSeek等主流AI服务。</p>',
        date: '2024-02-23',
        category: '桌面应用',
        isLiked: false,
        tags: ['Python', 'GUI', 'AI', 'API'],
        github: 'https://github.com/KercyDing/Nefelibata'
      },
      {
        id: 4,
        title: 'Chess-with-AI - 智能国际象棋游戏',
        excerpt: '基于pygame开发的国际象棋游戏，集成AI算法实现人机对战。这是我在游戏开发和AI算法方面的探索。',
        content: '<p>这是一个完整的国际象棋游戏项目...</p><p>包含AI算法实现，支持不同难度的人机对战。</p>',
        date: '2024-02-10',
        category: '游戏开发',
        isLiked: false,
        tags: ['Python', 'Pygame', 'AI', 'Game'],
        github: 'https://github.com/KercyDing/Chess-with-AI'
      }
    ])

    // 项目分类数据
    const categories = ref([
      { name: '学习项目', icon: 'fas fa-graduation-cap', count: 1 },
      { name: '移动开发', icon: 'fas fa-mobile-alt', count: 1 },
      { name: '桌面应用', icon: 'fas fa-desktop', count: 1 },
      { name: '游戏开发', icon: 'fas fa-gamepad', count: 1 }
    ])

    // 技术标签云（3D球体版本） - 增加更多标签
    const tags = ref([
      { name: 'Python', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'Flutter', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'C++', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'AI/ML', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'Dart', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'Qt', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'Algorithm', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'Mobile', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'Vue.js', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'JavaScript', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'Game Dev', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'GUI', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'Stanford', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'Pygame', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'HTML5', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'CSS3', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'Node.js', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'Git', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'VS Code', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'React', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'TypeScript', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'Linux', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'Database', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 },
      { name: 'API', x: 0, y: 0, z: 0, originalX: 0, originalY: 0, originalZ: 0, targetX: 0, targetY: 0, targetZ: 0 }
    ])

    // 个人简介
    const personalInfo = ref({
      name: 'Kercy Ding',
      university: 'HFUT',
      description: '一个热爱IT的极客罢了。',
      github: 'https://github.com/KercyDing'
    })

    // 技术栈
    const techStack = ref([
      'Python', 'C++', 'JavaScript', 'Flutter', 'Dart', 'Vue.js', 
      'Qt', 'Pygame', 'AI/ML', 'Algorithm', 'Git', 'VS Code'
    ])

    // 计算属性
    const archiveData = computed(() => {
      const archive = {}
      blogPosts.value.forEach(post => {
        const year = new Date(post.date).getFullYear()
        const month = new Date(post.date).toLocaleDateString('zh-CN', { month: 'long' })
        
        if (!archive[year]) {
          archive[year] = {}
        }
        if (!archive[year][month]) {
          archive[year][month] = []
        }
        archive[year][month].push(post)
      })
      
      return Object.keys(archive).map(year => ({
        year,
        months: Object.keys(archive[year]).map(month => ({
          month,
          posts: archive[year][month]
        }))
      }))
    })

    // 3D标签球初始化 - 重新设计的球面分布算法
    const initTagSphere = () => {
      const radius = 130 // 球体半径
      const total = tags.value.length
      
      tags.value.forEach((tag, index) => {
        // 特殊处理API和Python标签，避免它们在两极
        if (tag.name === 'API') {
          // 将API放在赤道偏北30度的位置
          const theta = Math.PI * 0.3 // 约54度方位角
          const phi = Math.PI * 0.33 // 北纬30度
          const x = radius * Math.sin(phi) * Math.cos(theta)
          const z = radius * Math.sin(phi) * Math.sin(theta)
          const actualY = radius * Math.cos(phi)
          
          tag.x = tag.originalX = tag.targetX = x
          tag.y = tag.originalY = tag.targetY = actualY
          tag.z = tag.originalZ = tag.targetZ = z
          return
        }
        
        if (tag.name === 'Python') {
          // 将Python放在赤道偏南45度的位置
          const theta = Math.PI * 1.2 // 约216度方位角
          const phi = Math.PI * 0.75 // 南纬45度
          const x = radius * Math.sin(phi) * Math.cos(theta)
          const z = radius * Math.sin(phi) * Math.sin(theta)
          const actualY = radius * Math.cos(phi)
          
          tag.x = tag.originalX = tag.targetX = x
          tag.y = tag.originalY = tag.targetY = actualY
          tag.z = tag.originalZ = tag.targetZ = z
          return
        }
        
        // 使用更科学的球面均匀分布算法（Fibonacci球面）
        const goldenAngle = Math.PI * (3 - Math.sqrt(5)) // 黄金角
        const theta = goldenAngle * index // 方位角
        let y = 1 - (index / (total - 1)) * 2 // 从1到-1的均匀分布
        
        // 避免其他标签过于接近两极（限制y值范围）
        y = Math.max(-0.85, Math.min(0.85, y))
        
        const radiusAtY = Math.sqrt(1 - y * y) // 当前纬度的半径
        
        // 计算3D坐标
        const x = Math.cos(theta) * radiusAtY * radius
        const z = Math.sin(theta) * radiusAtY * radius
        const actualY = y * radius
        
        // 保存原始球面位置（不倾斜）
        tag.x = tag.originalX = tag.targetX = x
        tag.y = tag.originalY = tag.targetY = actualY
        tag.z = tag.originalZ = tag.targetZ = z
      })
      
      // 初始化经纬线网格
      initGrid()
    }

    // 初始化经纬线网格
    const initGrid = () => {
      const gridContainer = document.getElementById('gridContainer')
      if (!gridContainer) return
      
      gridContainer.innerHTML = '' // 清空现有网格
      
      const radius = 130
      // const earthTiltDeg = 23.5 // 地轴倾斜角度，已在setup()中定义为earthTilt
      
      // 创建经线（meridians）- 从北极到南极的半圆
      for (let i = 0; i < 18; i++) {
        const longitude = (i * 20) * Math.PI / 180 // 每20度一条经线，总共18条
        const meridian = document.createElement('div')
        meridian.className = 'meridian-line'
        
        // 生成经线上的点
        let pathPoints = []
        for (let j = 0; j <= 72; j++) {
          const latitude = (j * 2.5 - 90) * Math.PI / 180 // -90度到90度，更密集的点
          const x = radius * Math.cos(latitude) * Math.cos(longitude)
          const y = radius * Math.sin(latitude)
          const z = radius * Math.cos(latitude) * Math.sin(longitude)
          pathPoints.push({x, y, z})
        }
        
        meridian.dataset.points = JSON.stringify(pathPoints)
        gridContainer.appendChild(meridian)
      }
      
      // 创建纬线（parallels）- 水平圆环
      const latitudes = [-75, -60, -45, -30, -15, 0, 15, 30, 45, 60, 75] // 更多纬线
      latitudes.forEach(lat => {
        const latRad = lat * Math.PI / 180
        const parallel = document.createElement('div')
        parallel.className = 'parallel-line'
        
        // 生成纬线上的点
        let pathPoints = []
        const latRadius = radius * Math.cos(latRad)
        const y = radius * Math.sin(latRad)
        
        for (let i = 0; i <= 144; i++) {
          const longitude = (i * 2.5) * Math.PI / 180 // 每2.5度一个点，更密集
          const x = latRadius * Math.cos(longitude)
          const z = latRadius * Math.sin(longitude)
          pathPoints.push({x, y, z})
        }
        
        parallel.dataset.points = JSON.stringify(pathPoints)
        parallel.dataset.latitude = lat
        gridContainer.appendChild(parallel)
      })
    }

    // 更新经纬线网格显示
    const updateGrid = (time) => {
      const gridContainer = document.getElementById('gridContainer')
      if (!gridContainer) return
      
      // 更新经线
      const meridians = gridContainer.querySelectorAll('.meridian-line')
      meridians.forEach((meridian) => {
        try {
          const points = JSON.parse(meridian.dataset.points)
          let visiblePoints = []
          
          points.forEach(point => {
            // 应用地轴倾斜
            let rotatedX = point.x
            let rotatedY = point.y * Math.cos(earthTilt) - point.z * Math.sin(earthTilt)
            let rotatedZ = point.y * Math.sin(earthTilt) + point.z * Math.cos(earthTilt)
            
            // 应用旋转（逆时针方向）
            const finalX = rotatedX * Math.cos(-time) - rotatedZ * Math.sin(-time)
            const finalZ = rotatedX * Math.sin(-time) + rotatedZ * Math.cos(-time)
            
            // 3D到2D投影
            const perspective = 400
            const projectedZ = finalZ + perspective
            if (projectedZ > 0) {
              const scale = perspective / projectedZ
              const screenX = finalX * scale
              const screenY = rotatedY * scale
              
              visiblePoints.push({
                x: screenX,
                y: screenY,
                z: finalZ,
                opacity: Math.max(0.1, Math.min(1, (finalZ + 130) / 260))
              })
            }
          })
          
          // 更新经线显示
          updateLineDisplay(meridian, visiblePoints)
        } catch (e) {
          console.error('Error updating meridian:', e)
        }
      })
      
      // 更新纬线
      const parallels = gridContainer.querySelectorAll('.parallel-line')
      parallels.forEach(parallel => {
        try {
          const points = JSON.parse(parallel.dataset.points)
          let visiblePoints = []
          
          points.forEach(point => {
            // 应用地轴倾斜
            let rotatedX = point.x
            let rotatedY = point.y * Math.cos(earthTilt) - point.z * Math.sin(earthTilt)
            let rotatedZ = point.y * Math.sin(earthTilt) + point.z * Math.cos(earthTilt)
            
            // 应用旋转（逆时针方向）
            const finalX = rotatedX * Math.cos(-time) - rotatedZ * Math.sin(-time)
            const finalZ = rotatedX * Math.sin(-time) + rotatedZ * Math.cos(-time)
            
            // 3D到2D投影
            const perspective = 400
            const projectedZ = finalZ + perspective
            if (projectedZ > 0) {
              const scale = perspective / projectedZ
              const screenX = finalX * scale
              const screenY = rotatedY * scale
              
              visiblePoints.push({
                x: screenX,
                y: screenY,
                z: finalZ,
                opacity: Math.max(0.1, Math.min(1, (finalZ + 130) / 260))
              })
            }
          })
          
          // 更新纬线显示
          updateLineDisplay(parallel, visiblePoints)
        } catch (e) {
          console.error('Error updating parallel:', e)
        }
      })
    }

    // 更新线条显示
    const updateLineDisplay = (element, points) => {
      if (points.length < 2) {
        element.style.display = 'none'
        return
      }
      
      element.style.display = 'block'
      
      // 使用CSS的clip-path或transform来显示线条
      // 这里使用简化的方法：只显示第一个点的位置和透明度
      const firstPoint = points[0]
      const avgOpacity = points.reduce((sum, p) => sum + p.opacity, 0) / points.length
      
      element.style.transform = `translate(${firstPoint.x}px, ${firstPoint.y}px)`
      element.style.opacity = avgOpacity * 0.3 // 降低网格线的透明度
    }

    // 3D标签球动画 - 重新设计的地球仪旋转效果
    const animateTagSphere = () => {
      const sphere = document.getElementById('tagSphere')
      if (!sphere) {
        animationId = requestAnimationFrame(animateTagSphere)
        return
      }

      const time = Date.now() * 0.0005 * rotationSpeed.value // 旋转速度
      
      // 更新经纬线网格
      updateGrid(time)
      
      tags.value.forEach((tag, index) => {
        const element = sphere.children[index]
        if (!element) return

        // 从原始球面位置开始，绕Y轴反向旋转（逆时针方向）
        const rotatedX = tag.targetX * Math.cos(-time) - tag.targetZ * Math.sin(-time)
        const rotatedZ = tag.targetX * Math.sin(-time) + tag.targetZ * Math.cos(-time)
        const rotatedY = tag.targetY
        
        // 由于地轴是竖直的，不需要额外的倾斜变换
        const finalX = rotatedX
        const finalY = rotatedY
        const finalZ = rotatedZ
        
        // 3D投影到2D（透视投影）
        const perspective = 400
        const projectedZ = finalZ + perspective
        const scale = perspective / projectedZ
        
        // 计算屏幕坐标
        const screenX = finalX * scale
        const screenY = finalY * scale
        
        // 修正：scale应该与Z坐标正相关，越靠前(rotatedZ越大)应该越大
        const correctedScale = Math.max(0.4, Math.min(1.2, 0.6 + (rotatedZ + 130) / 260 * 0.6))
        element.style.transform = `translate3d(${screenX}px, ${screenY}px, 0) scale(${correctedScale})`
        
        // 根据悬停状态和Z坐标设置样式
        if (hoveredTag.value === index) {
          // 悬停的标签继续轨道运动，但有简单的高亮效果
          element.style.opacity = 1
          element.style.zIndex = 9999
          element.style.pointerEvents = 'auto'
          
          // 简单的悬停效果：亮色文字 + 微妙发光
          element.style.color = '#00d4ff'
          element.style.textShadow = '0 0 8px rgba(0, 212, 255, 0.6)'
          element.style.fontWeight = '700'
          
          // 轻微放大效果
          element.style.transform = `
            translate3d(${screenX}px, ${screenY}px, 0) 
            scale(${correctedScale * 1.1})
          `
        } else {
          // Z坐标归一化到透明度和层级
          const normalizedZ = (rotatedZ + 130) / 260
          const opacity = Math.max(0.3, Math.min(1, 0.5 + normalizedZ * 0.5))
          element.style.opacity = opacity
          
          // Z值越大（越靠前）应该有更高的z-index
          const zIndex = Math.floor(1000 + normalizedZ * 1000)
          element.style.zIndex = zIndex
          
          // 修正鼠标事件：只有前半球的标签可以响应鼠标事件
          element.style.pointerEvents = (rotatedZ > -30 && opacity > 0.5) ? 'auto' : 'none'
          
          // 恢复默认样式
          element.style.color = 'rgba(255, 255, 255, 0.9)'
          element.style.textShadow = '0 2px 4px rgba(0, 0, 0, 0.6)'
          element.style.fontWeight = '600'
        }
      })

      animationId = requestAnimationFrame(animateTagSphere)
    }

    // 标签悬停处理 - 添加防抖动机制
    const onTagHover = (index) => {
      // 防止快速切换导致的抖动
      if (hoveredTag.value === index) return
      hoveredTag.value = index
    }

    // 标签离开处理 - 添加小延迟避免抖动
    const onTagLeave = () => {
      // 添加小延迟，避免鼠标快速移动时的抖动
      setTimeout(() => {
        hoveredTag.value = -1
      }, 10)
    }

    // 鼠标移动检测（保留但不影响标签位置）
    const onMouseMove = () => {
      // 只用于检测鼠标活动，不执行任何移动逻辑
    }

    // 生命周期钩子
    onMounted(() => {
      initTagSphere()
      setTimeout(() => {
        animateTagSphere()
      }, 100)
    })

    onUnmounted(() => {
      if (animationId) {
        cancelAnimationFrame(animationId)
      }
    })

    // 方法
    const toggleMenu = () => {
      isMenuOpen.value = !isMenuOpen.value
    }

    const setActiveTab = (tab) => {
      activeTab.value = tab
      isMenuOpen.value = false
    }

    const openPost = (post) => {
      selectedPost.value = post
    }

    const closePost = () => {
      selectedPost.value = null
    }

    // 搜索功能
    const filteredPosts = computed(() => {
      let posts = blogPosts.value
      
      // 分类筛选
      if (selectedCategory.value) {
        posts = posts.filter(post => post.category === selectedCategory.value)
      }
      
      // 搜索筛选
      if (searchQuery.value.trim()) {
        const query = searchQuery.value.toLowerCase().trim()
        posts = posts.filter(post => 
          post.title.toLowerCase().includes(query) ||
          post.excerpt.toLowerCase().includes(query) ||
          post.category.toLowerCase().includes(query) ||
          post.tags.some(tag => tag.toLowerCase().includes(query))
        )
      }
      
      return posts
    })

    const searchPosts = () => {
      // 搜索功能已通过计算属性实现，这里可以添加搜索分析等逻辑
      console.log('搜索:', searchQuery.value, '结果数量:', filteredPosts.value.length)
    }

    const filterByCategory = (categoryName) => {
      console.log('按分类筛选:', categoryName)
      selectedCategory.value = categoryName
      setActiveTab('home')
    }

    const clearCategoryFilter = () => {
      selectedCategory.value = null
    }

    const filterByTag = (tagName) => {
      console.log('按标签筛选:', tagName)
      setActiveTab('home')
    }

    return {
      isMenuOpen,
      activeTab,
      selectedPost,
      searchQuery,
      selectedCategory,
      blogPosts,
      filteredPosts,
      categories,
      tags,
      personalInfo,
      techStack,
      archiveData,
      hoveredTag,
      toggleMenu,
      setActiveTab,
      openPost,
      closePost,
      searchPosts,
      filterByCategory,
      clearCategoryFilter,
      filterByTag,
      onTagHover,
      onTagLeave,
      onMouseMove
    }
  }
}
</script>

