  animation: spherePulse 4s ease-in-out infinite;
  pointer-events: none;
}

/* 地轴 - 穿过南北极，竖直方向，细虚线样式 */
.earth-axis {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 2px;
  height: 300px; /* 比球体直径(260px)稍长 */
  background: transparent;
  transform: translate(-50%, -50%);
  pointer-events: none;
  animation: axisGlow 3s ease-in-out infinite alternate;
}

.earth-axis::before {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  width: 0;
  height: 100%; /* 完整长度 */
  border-left: 2px dashed rgba(255, 255, 255, 0.6); /* 加粗到2px */
  transform: translateX(-50%);
  animation: inherit; /* 继承父元素的动画 */
}

/* 经纬线网格容器 */
.grid-container {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 260px;
  height: 260px;
  transform: translate(-50%, -50%);
  pointer-events: none;
  transform-style: preserve-3d;
}

/* 经线样式 */
.meridian-line {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 2px;
  height: 2px;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 50%;
  pointer-events: none;
  transition: opacity 0.1s ease;
  box-shadow: 0 0 4px rgba(255, 255, 255, 0.2);
}

/* 纬线样式 */
.parallel-line {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 2px;
  height: 2px;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  pointer-events: none;
  transition: opacity 0.1s ease;
  box-shadow: 0 0 3px rgba(255, 255, 255, 0.15);
}

/* 球体光晕效果 */
.sphere-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 320px;
  height: 320px;
  border-radius: 50%;
  background: radial-gradient(circle, 
    rgba(59, 130, 246, 0.05) 0%, 
    rgba(37, 99, 235, 0.03) 30%,
    rgba(29, 78, 216, 0.02) 60%,
    transparent 80%);
  transform: translate(-50%, -50%);
  animation: sphereGlow 6s ease-in-out infinite alternate;
  pointer-events: none;
}

/* 水平椭圆 - 与球体相切 */
.horizontal-ellipse {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 320px;
  height: 40px;
  border: 2px solid rgba(255, 255, 255, 0.25);
  border-radius: 50%;
  background: transparent;
  transform: translate(-50%, calc(-50% + 150px)); /* 向下移动150px使椭圆与球体相切 */
  animation: spherePulse 4s ease-in-out infinite;
  pointer-events: none;
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.1);
}

/* 3D标签球核心容器 */
.tag-sphere {
  position: absolute;
  top: 50%;
  left: calc(50% - 30px); /* 向左移动30px */
  width: 260px;
  height: 260px;
  transform: translate(-50%, -50%);
  transform-style: preserve-3d;
  pointer-events: none;
}

/* 3D标签样式 */
.tag-3d {
  position: absolute;
  top: 50%;
  left: 50%;
  background: transparent;
  color: rgba(255, 255, 255, 0.9);
  padding: 0.4rem 0.8rem;
  border-radius: 20px;
  cursor: pointer;
  /* 移除可能引起抖动的过渡效果 */
  transition: none;
  font-weight: 600;
  font-size: 0.85rem;
  border: none;
  white-space: nowrap;
  transform-origin: center;
  user-select: none;
  letter-spacing: 0.3px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.6);
  pointer-events: auto;
  /* 确保标签不会被其他元素遮挡 */
  position: absolute;
  isolation: isolate;
}

/* 悬停状态的标签 - 只有简单的高亮效果，保持轨道运动 */
.tag-3d.tag-hovered {
  background: transparent !important;
  color: #00bfff !important;
  border: none !important;
  z-index: 9999 !important;
  pointer-events: auto !important;
  text-shadow: 0 0 10px rgba(0, 191, 255, 0.8) !important;
  filter: none !important;
  /* 保持absolute定位，确保3D变换正常工作 */
  position: absolute !important;
  /* 不阻止动画，让标签继续轨道运动 */
}

/* 动画关键帧 */
@keyframes spherePulse {
  0%, 100% { 
    opacity: 0.3;
    transform: translate(-50%, -50%) scale(1);
    border-color: rgba(255, 255, 255, 0.3);
  }
  50% { 
    opacity: 0.5;
    transform: translate(-50%, -50%) scale(1.01);
    border-color: rgba(255, 255, 255, 0.5);
  }
}

@keyframes sphereGlow {
  0% { 
    opacity: 0.1;
    transform: translate(-50%, -50%) scale(0.98);
  }
  100% { 
    opacity: 0.2;
    transform: translate(-50%, -50%) scale(1.02);
  }
}

@keyframes axisGlow {
  0% {
    opacity: 0.5;
  }
  100% {
    opacity: 0.8;
  }
}

